import {
  PositiveDecimal,
  TOTAL_PERFORMANCE_PERCENTAGE_PRECISION,
  TOTAL_PERFORMANCE_PERCENTAGE_SCALE,
} from './BaseNumber';

export class TotalPerformancePercentage extends PositiveDecimal<'TotalPerformancePercentage'> {
  static from(value: string): TotalPerformancePercentage {
    return new TotalPerformancePercentage({
      value,
      precision: TOTAL_PERFORMANCE_PERCENTAGE_PRECISION,
      scale: TOTAL_PERFORMANCE_PERCENTAGE_SCALE,
    });
  }
  static new(): TotalPerformancePercentage {
    return new TotalPerformancePercentage({
      value: '0.0',
      precision: TOTAL_PERFORMANCE_PERCENTAGE_PRECISION,
      scale: TOTAL_PERFORMANCE_PERCENTAGE_SCALE,
    });
  }
}
